.block--local-tasks-block {
  z-index: 10;
  background: #f5f5f2;
  border-top: 1px solid #aaa;
  font-family: Source Sans Pro, Lucida Grande, Verdana, sans-serif;
  font-size: 13px;
  font-weight: normal;
  position: fixed;
  inset: auto 0 0;
}

@media (max-width: 999.98px) {
  .block--local-tasks-block {
    display: none;
  }
}

body.toolbar-hidden .block--local-tasks-block {
  display: none;
}

.block--local-tasks-block ul {
  flex-flow: wrap;
  justify-content: center;
  padding: 0;
  list-style: none;
  display: flex;
}

.block--local-tasks-block li:first-child {
  margin-left: 0;
}

.block--local-tasks-block a {
  color: #565656;
  padding: 9px 18px;
  text-decoration: none;
  display: block;
}

.block--local-tasks-block a:hover {
  color: #000;
  background: #fff;
}

.block--local-tasks-block a.is-active {
  color: #000;
  font-weight: bold;
}

body.toolbar-hidden {
  padding-bottom: 0 !important;
}

